 @import './components/variables/colors.scss';
 @import './components/fonts/fonts.scss';
 @import './components/input/input.scss';
 h1 {
     font-family: 'Quicksand';
     font-style: normal;
     font-weight: bold;
     font-size: 24px;
     line-height: 24px;
     color: $dark-shade-100;
 }
 
 h2 {
     font-family: 'Quicksand';
     font-style: normal;
     font-weight: bold;
     font-size: 19px;
     line-height: 24px;
     color: $dark-shade-100;
 }
 
 h3 {
     font-family: 'Montserrat';
     font-style: normal;
     font-weight: bold;
     font-size: 12px;
     line-height: 15px;
     color: $dark-shade-100;
     text-transform: uppercase;
 }
 
 body {
     font-family: 'Montserrat';
     font-style: normal;
     font-weight: normal;
     font-size: 14px;
     line-height: 24px;
     color: $dark-shade-75;
 }